import PropTypes from 'prop-types';

import {
  ButtonStyled
} from './styles';

function Button({ children, type }) {
  return (<ButtonStyled type={type}>{children}</ButtonStyled>);
}

Button.defaultProps = {
  children: '',
  type: 'default'
};

Button.propTypes = {
  children: PropTypes.string,
  type: PropTypes.string
};

export default Button;
